*{
    margin: 0;
}
.NavTabeleTD{
    color: black;
    text-decoration: none;
    font-size: large;
    height: 100%;
  }

  .NavBtn{
     height: 100%;
     width: 100px;
     background: red;
     outline: none;
     font-size: 20px;
     font-family: 'Anton', sans-serif;
     font-family: 'Roboto Slab', serif;
     border: dotted;
    
 }

 .drop-down-content{
     position: absolute;
z-index: 1;
padding-top: 10px;

display: none;

 }
 
 .NavBtnDown{
     margin-top: 10px;
    height: 100%;
    width: 100px;
    background: red;
    outline: none;
    font-size: 20px;
    font-family: 'Anton', sans-serif;
    font-family: 'Roboto Slab', serif;
    border: dotted;

 
 }
 #OptionstoDropDown{
    text-decoration: none;
     display: block;
   font-size: 12px;
 }

 #ContentNav:hover .drop-down-content{
   display: block;
    
 }
 #ContentNav:hover  #OptionstoDropDown{
   display: none;
 }